type CadenzaboxAutocompleteAllowedTypes =
  | 'tag'
  | 'release'
  | 'composer'
  | 'track'

interface CadenzaboxAutocompleteResultsTypeTagInterface {
  category?: string
  parent?: string
  name?: string
  id?: string // Select key ?tags=value
}

export interface CadenzaboxAutocompleteResultsTypeComposerInterface {
  firstName?: string
  lastName?: string
  middleName?: string
  slug?: string // Select key ?composers=value
}

interface CadenzaboxAutocompleteResultsTypeReleaseInterface {
  title?: string
  totalTracks?: number
  count?: number
  slug?: string // Select key ?releases=value
}

interface CadenzaboxAutocompleteResultsTypeTrackInterface {
  title?: string
  slug?: string // Select key ?tracks=value
}

export enum CadenzaboxAutocompleteMapTypeByQuery {
  tags = 'tag',
  releases = 'release',
  composers = 'composer',
  tracks = 'track',
  q = 'q'
}

export interface CadenzaboxAutocompleteInterface extends CadenzaboxAutocompleteResultsTypeTagInterface, CadenzaboxAutocompleteResultsTypeComposerInterface, CadenzaboxAutocompleteResultsTypeReleaseInterface, CadenzaboxAutocompleteResultsTypeTrackInterface {
  _id: string
  type: CadenzaboxAutocompleteAllowedTypes
  searchIndex: string
  order?: number // Used for custom order when grouping by type
}

export interface CadenzaboxAutocompleteResultsGroupedByTypeInterface {
  [key: string]: CadenzaboxAutocompleteInterface[]
}
